import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading } from 'ionic-angular';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';

/**
 * Generated class for the TermsAndConditionsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-terms-and-conditions',
  templateUrl: 'terms-and-conditions.html',
})
export class TermsAndConditionsPage {

  loading: Loading; // for loader
  termsText: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private commonProvider: CommonServiceProvider, public loadingCtrl: LoadingController, private reservationProvider: ReservationServiceProvider) {
  }

  ionViewDidLoad() {
    this.showLoading();
    // get cancellation policy text
    let reservationId = this.reservationProvider.objReservation.reservationId;
    this.commonProvider.getTermsAndConditions(reservationId).subscribe(data => {
      this.loading.dismiss();
      if (data.success == true) {
        this.termsText = data.response;
      }

    });
    console.log('ionViewDidLoad TermsAndConditionsPage');
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }
  
}



